    <h4 class="fw-bold">Sztandar na gruzach, wśród chmur</h4>
    <p class="fw-bold">
      Świt 18 maja 1944 roku zapowiadał kolejny słoneczny dzień. Wymarzona pogoda. Ale nie wokół ruin klasztoru na Monte
      Cassino. Taka aura oznaczała, że w upale znowu przyjdzie ginąć dziesiątkom polskich żołnierzy szturmujących to
      wzgórze. Jednak ten ranek był inny. Nad pobojowiskiem panowała cisza przerywana jedynie, od czasu do czasu,
      dalekim wystrzałem armatnim.
    </p>
    <p>
      Taka cisza była w tym strasznym miejscu czymś nienaturalnym. Zażarte walki o wzgórza, broniące aliantom drogi do
      Rzymu, trwały już kilka długich miesięcy. Od stycznia 1944 roku wojska amerykańskie, brytyjskie, francuskie i
      nowozelandzkie starały się wyrzucić doborowe oddziały niemieckich strzelców spadochronowych i górskich z ich
      pozycji obronnych. Na próżno. Niemcy dziesiątkowali żołnierzy alianckich z precyzyjną regularnością. Na nic zdała
      się tutaj przewaga lotnicza. Zbombardowanie klasztoru na Monte Cassino tylko uczyniło sytuację niemieckich
      oddziałów jeszcze korzystniejszą – w ruinach łatwiej było odpierać kolejne szturmy, a te załamywały się jeden po
      drugim. Nawet najbitniejsi z bitnych – Gurkhowie nie dali rady przebić się przez ogień„zielonych diabłów”, jak
      nazywano niemieckich spadochroniarzy.
      Kiedy 12 maja do ataku ruszyli żołnierze 2 Korpusu Polskiego, wyglądało na to, że kolejny raz potwierdzi się
      reguła znana od starożytności, iż te przeklęte wzgórza są nie do zdobycia. Istotnie, polskie natarcie załamało
      się, ale to było dopiero pierwsze podejście. Polacy mieli z Niemcami znacznie większe rachunki do wyrównania niż
      Anglosasi, Francuzi, Hindusi, Kanadyjczycy, Marokańczycy, Morysi czy Nowozelandczycy… Krwawili na swych
      wywalczonych pozycjach, ale trzymali je i 16 maja znowu ruszyli na Widmo, San Angelo, Górę Ofiarną… Niemcy też już
      nie byli tak pewni swego, jak wcześniej. Dobrze znali swego odwiecznego wroga. I śmierć zaczęła ich dziesiątkować
      – polska śmierć… O tym wszystkim myślał podporucznik Kazimierz Gurbiel, chłonąc tę nienaturalną ciszę. Lecz w
      końcu zmęczenie wzięło górę i oficer pogrążył się w drzemce. Zarówno on, jak i jego towarzysze z 12 Pułku Ułanów
      Podolskich, wspomagali w walce piechotę ze swej karpackiej dywizji. Nie tylko szturmowali niemieckie bunkry, ale i
      prowadzili nieprzerwanie patrole bojowe, kiedy piechota przegrupowywała się do kolejnego natarcia.
    </p>
    <h4 class="fw-bold">Ostatni patrol</h4>
    <p>
      Ze snu wyrwał podporucznika Gurbiela znajomy głos. To dowódca jego szwadronu mówił, że wzywa go porucznik
      Mieczysław Sander. „Pewnie znowu patrol, ta cisza jednak nie przyniosła niczego nowego” – pomyślał Gurbiel, ale
      tym razem jego frontowe przeczucie było niespójne. Miał to być zupełnie inny patrol niż poprzednie.
      Kiedy Gurbiel zameldował się u porucznika, ten bez żadnych wstępów od razu wyłuszczył, o co chodzi: „Kaziu, wal do
      klasztoru. Spenetruj, co i jak. Powiadają, że Niemców już nie ma. Nie wierz. Bądź ostrożny jakby tam jeszcze
      siedzieli”. Okazało się, że nad ranem zauważono na ruinach klasztoru białą flagę. Zelektryzowany takim rozkazem
      Gurbiel nie wrócił po swój hełm, bluzę i thompsona, tylko tak jak stał, w koszuli, w furażerce na głowie i z
      coltem w kaburze poszedł na czele trzynastu podoficerów w kierunku ruin klasztoru. Mijali kikuty drzew oliwnych
      poszarpanych ogniem i ostrożnie stąpali między kamieniami i kępami czerwonych maków. Trzeba było uważać nie tylko
      na rozkładające się w słońcu ciała poległych, ale i na miny. W każdej chwili też mogli zostać skoszeni serią
      spandaua lub rozsiekani pociskiem moździerzowym. Dobrze już poznali metody walki zielonych diabłów, którzy już nie
      raz tutaj wykorzystywali białą flagę – znak kapitulacji, jako fortel do niespodziewanego ataku. Ale tym razem nic
      takiego nie nastąpiło.
      Po kilku godzinach kluczenia, ścieżką między skałami, których nie mogła zdobyć cała aliancka 15 Grupa Armii, około
      dziesiątej ułani stanęli przed grubymi i poszczerbionymi murami benedyktyńskiej kolebki chrześcijaństwa. Podeszli
      do najmniej zniszczonego budynku i kiedy podporucznik Gurbiel pchnął jego drzwi, poczuli straszny fetor. W środku
      leżało 16 ciężko rannych spadochroniarzy, przy których stało kilku sanitariuszy. To byli twardzi żołnierze, ale
      ich oczy zdradzały przerażenie, jakie budziła w nich świadomość, że przyjdą po nich Polacy i będą się mścić za ich
      zbrodnie w Polsce. Ci jednak nie zniżyli się do poziomu elity Hitlera. Podporucznik Gurbiel przez tłumacza
      oświadczył krótko: „Włos wam z głowy nie spadnie”. Zszedł następnie do piwnicy pod izbą sanitarną i włosy stanęły
      mu dęba pod furażerką: kazamaty zawalone były skrzyniami z poszarpanymi trupami strzelców spadochronowych, a
      między nimi leżało jeszcze trzech ciężko rannych, których pozostawiono tam, by dogorywali.
    </p>
    <h4 class="fw-bold">Zwycięstwo</h4>
    <p>
      Podporucznik Gurbiel wybiegł na zewnątrz, by zaczerpnąć świeżego powietrza i ochłonąć ze zgrozy, która mogła
      poruszyć najbardziej zaprawionego w bojach weterana. Kiedy chodził między ruinami, nagle zauważył, że pod górę
      klasztorną pnie się jakichś dwóch ludzi. Okazali się nimi oficer kanadyjski i niemiecki jeniec w roli przewodnika.
      Kanadyjczyk nie potrafił ukryć rozczarowania, że to nie jemu przypadła palma pierwszeństwa stanięcia w
      najsłynniejszej tej wiosny reducie nieprzyjaciela. Za Kanadyjczykiem na szczyt dotarły ułańskie posiłki – pluton
      porucznika Leona Hrynkiewicza. Jeden z jego ułanów, Józef Bruliński wyciągnął zza bluzy naprędce sporządzony z
      czerwonej i granatowej chusty oraz z bandaży proporzec 12 Pułku Ułanów Podolskich i zawiesił wysoko na żerdzi. I
      tak amarantowo-biało-granatowe barwy noszone przez ułanów podolskich od czasów napoleońskich stały się pierwszym
      znakiem obwieszczającym światu polskie zwycięstwo na Monte Cassino. Niedługo zastąpiono go biało-czerwoną flagą,
      obok której na rozkaz gen. Władysława Andersa powieszono i flagę brytyjską. O godzinie 11.15 podporucznik Tadeusz
      Drabczyński wypuścił gołębia pocztowego, który przyniósł do sztabu 2 Korpusu Polskiego meldunek o zajęciu wzgórza
      klasztornego. Na kartce napisana była tylko jedna wielka litera „V” – jak victory. W samo południe z ruin
      klasztoru popłynęła melodia hejnału mariackiego, odegrana przez plutonowego Emila Czecha. Tak polscy żołnierze
      uczcili zajęcie kluczowej pozycji w linii Gustawa.
      Choć na okolicznych wzgórzach Polacy, wciąż walczyli z wycofującymi się niemieckimi spadochroniarzami, na ręce
      gen. Andersa posypały się gratulacje. Podkreślano w nich, że niezłomność jego Korpusu dała wygraną w całej bitwie
      o Monte Cassino. Komplementowano polskich żołnierzy, przydając im pierwsze miejsce pośród wszystkich alianckich
      narodów.
      Zgodnie z przewidywaniami gen. Andersa, do lamusa poszła sowiecka propaganda o tym, że Polacy nie chcą bić się z
      Niemcami. Sprawa polska znowu znalazła się na pierwszych stronach gazet. Wydawało się, że to przełomowe zwycięstwo
      choć okupione wielkimi stratami, wreszcie obudzi sumienie włodarzy zachodniego świata i powstrzymają oni zaborcze
      zapędy Stalina wobec Polski. Niestety, choć 2 Korpus Polski jeszcze nie raz triumfował w Italii, Amerykanie i
      Brytyjczycy nie zamierzali drażnić Stalina – dla nich ważniejszy był potencjał Armii Czerwonej, bez którego nie
      wyobrażali sobie wygrania tej wojny. Lecz nic już nie mogło wymazać pamięci o polskiej bitwie pod Monte Cassino –
      weszła ona do narodowego panteonu:
    </p>
    <blockquote>
      Runęli przez ogień straceńcy,<br>
      Niejeden z nich dostał i padł,<br>
      Jak ci z Somosierry szaleńcy,<br>
      Jak ci spod Racławic sprzed lat.<br>
      Runęli impetem szalonym<br>
      I doszli. I udał się szturm.<br>
      I sztandar swój biało-czerwony<br>
      Zatknęli na gruzach wśród chmur.<br>
    </blockquote>

